<template>
  <el-menu
    class="sidebar-el-menu"
    :default-active="onRoutes"
    :collapse="collapse"
    background-color="#324157"
    text-color="#bfcbd9"
    active-text-color="#20a0ff"
    router
  >
    <Submenu :submenu="submenuData" />
  </el-menu>
</template>

<script>
import bus from "@/utils/bus";
import Submenu from "./Submenu";
import submenuData from "./submenus";
export default {
  name: "Sider",
  data() {
    return {
      collapse: false,
      submenuData,
    };
  },
  computed: {
    onRoutes() {
      return this.$route.path.replace("/", "");
    },
  },
  components: {
    Submenu,
  },
  created() {
    bus.$on("collapse", (msg) => {
      this.collapse = msg;
    });
  },
  methods: {},
};
</script>

<style lang="scss" scoped>
.el-aside {
  height: 100%;
  display: block;
  overflow-y: scroll;
  .el-menu {
    height: 100%;
    border: 0;
  }
  &::-webkit-scrollbar {
    width: 0px;
  }
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 200px;
}
</style>
