<template>
  <div class="container">
    <Header></Header>
    <el-container :style="containerStyle">
      <Sider></Sider>
      <el-main>
        <div class="content">
          <transition name="move" mode="out-in">
            <keep-alive :include="tagsList">
              <router-view></router-view>
            </keep-alive>
          </transition>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "./Header";
import Sider from "./Sider";
export default {
  name: "Home",
  data() {
    return {
      headerHeight: "60px",
      tagsList: [],
      collapse: false,
    };
  },
  components: {
    Header,
    Sider,
  },
  computed: {
    containerStyle: function () {
      return {
        height: `calc(100% - ${this.headerHeight})`,
      };
    },
  },
  created() {},
  methods: {},
};
</script>

<style lang="scss" scoped>
.container {
  height: 100%;
  .el-main {
    padding: 12px;
    .content {
      height: 100%;
      border-radius: 5px;
      box-shadow: 2px 2px 8px 1px #bebaba;
      background-color: #fff;
      overflow-y: auto;
      overflow-x: hidden;
      &::-webkit-scrollbar {
        width: 3px;
        background: #ddd;
      }
      &::-webkit-scrollbar-thumb {
        width: 3px;
        height: 3px;
        background: #aaa;
        border-radius: 2px;
      }
    }
  }
}
</style>
