<script>
export default {
  name: "Submenu",
  functional: true, //render的context
  props: {
    submenu: {
      type: Array,
      default: () => [],
    },
  },
  render(h, context) {
    const { submenu } = context.props;
    // console.log(context);
    const vnodes = [];
    submenu.forEach((aItem, aKey) => {
      let title = h("template", { slot: "title" }, [
        h("i", { class: aItem.icon }),
        h("span", aItem.title),
      ]);
      let group = [];
      let children = [];

      if (aItem.group?.length > 0) {
        aItem.group.forEach((bItem, bKey) => {
          let itemTitle = h("template", { slot: "title" }, bItem.title);
          let groupItemMenus = [];
          bItem.items.forEach((cItem, cKey) => {
            let item = h(
              "el-menu-item",
              {
                props: {
                  index: cItem.path,
                  key: "cKey" + cKey,
                },
              },
              cItem.title
            );
            groupItemMenus.push(item);
          });
          let groupItem = h(
            "el-menu-item-group",
            {
              props: {
                index: bItem.path,
                key: "bKey" + bKey,
              },
            },
            [itemTitle, groupItemMenus]
          );
          group.push(groupItem);
        });
      }

      if (aItem.children?.length > 0) {
        aItem.children.forEach((dItem, dKey) => {
          let itemTitle = h("template", { slot: "title" }, dItem.title);
          let childItemMenus = [];
          if (dItem.items?.length > 0) {
            dItem.items.forEach((eItem, eKey) => {
              let item = h(
                "el-menu-item",
                {
                  props: {
                    index: eItem.path,
                    key: "eKey" + eKey,
                  },
                },
                eItem.title
              );
              childItemMenus.push(item);
            });
          }
          let childItem = null;
          if (!childItemMenus.length) {
            childItem = h(
              "el-menu-item",
              {
                props: {
                  index: dItem.path,
                  key: "dKey" + dKey,
                },
              },
              dItem.title
            );
          } else {
            childItem = h(
              "el-submenu",
              {
                props: {
                  index: dItem.path,
                  key: "dKey" + dKey,
                },
              },
              [itemTitle, childItemMenus]
            );
          }

          children.push(childItem);
        });
      }

      let itemNode = null;
      if (!group.length && !children.length) {
        itemNode = h(
          "el-menu-item",
          {
            props: {
              index: aItem.path,
              key: "aKey" + aKey,
            },
          },
          aItem.title
        );
      } else {
        itemNode = h(
          "el-submenu",
          {
            props: {
              index: aItem.path,
              key: "aKey" + aKey,
            },
          },
          [title, group, children]
        );
      }
      vnodes.push(itemNode);
    });
    // console.log(vnodes);
    return vnodes;
  },
};
</script>

